import React, {useEffect, useState} from 'react';
import PageHeader from '@/component/PageHeader'
import { useHistory} from 'react-router-dom'
import {
    Form,
    Input,
    Button,
    Radio,
    Select,
    Cascader,
    DatePicker,
    InputNumber,
    TreeSelect,
    Switch,
     message
} from 'antd';
import {connect} from "react-redux";
import { axiosPost} from '@/extend/axios/index.js'
import axios from 'axios'
import '@/assets/css/common.scss'
import {AndroidFilled,UploadOutlined } from "@ant-design/icons";
function Application(props){
    const [componentSize, setComponentSize] = useState('large');
    const record = props.location.state.record;
    console.log(props.location.state.record)
    console.log(record)

    const [form] = Form.useForm();
    const [visible,setVisible] = useState(record.shareWay)
    const history = useHistory();
    const onFinish = values => {
        console.log(values)
        axiosPost('/manage/wx/minipro/community/update',{
                    id:record.id,
                    type:values.type,
                    iconName:values.iconName,
                    name:values.name,
                    treatment:values.treatment,
                    sort:values.sort,
                    state:values.state,
                    welfare:values.welfare,
                    profile:values.profile,
                    specialGroup:values.specialGroup,
                    announcements:values.announcements,
                    adminWechatCode:values.adminWechatCode,
                    articleUrl:values.articleUrl,
                    articleImgUrl:values.articleImgUrl,
                    shareNumber:values.shareNumber,
                    joinNum:values.joinNum,
                    shareWay:values.shareWay,
                    payAmount:values.payAmount,
                    communityPeoples:values.communityPeoples

        }).then((res) => {
            if(res.code == '0'){
                        message.success("");
                        history.push("/wx/allcommunitys")
                    }else{
                        message.error(res.message);
                    }
        }).catch((error) => {
            console.log(error);
        });
    };
    const onChange = (e)=>{
        console.log(e.target.value)
        console.log("onchange:")
        setVisible(e.target.value);
        console.log(visible)
    }
    useEffect(() => {
        form.setFieldsValue(record);
    },[record]);
    return (
        <div className="application-box">
            <PageHeader title="" icon={AndroidFilled} />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-body">
                        <Form
                            labelCol={{ span: 4 }}
                            wrapperCol={{ span: 14 }}
                            layout="horizontal"
                            size={componentSize}
                            form={form}
                            onFinish={onFinish}
                        >

                            <Form.Item label="" name="type"   rules={[{ required: true }]}>
                                <Radio.Group >
                                    <Radio value={10}></Radio>
                                    <Radio value={20}></Radio>
                                    <Radio value={30}></Radio>
                                </Radio.Group>
                            </Form.Item>
                            <Form.Item label="" name="iconName" rules={[{ required: true, message: '!' }]}>
                                <Input type="text"  />
                            </Form.Item>
                            <Form.Item label="" name="name" rules={[{ required: true, message: '!' }]}>
                                <Input />
                            </Form.Item>
                            <Form.Item label="" name="profile" rules={[{ required: true, message: '!' }]}>
                                <Input.TextArea placeholder="" />
                            </Form.Item>
                            <Form.Item label="" name="joinNum" rules={[{type: 'number', min: 1, required: true, message: '!' }]} >
                                <InputNumber  />
                            </Form.Item>
                            <Form.Item name="sort" label="" rules={[{ type: 'number', min: 0,required: true, message: '!'}]} >
                                <InputNumber />
                            </Form.Item>
                            <Form.Item label="" name="state" rules={[{ required: true, message: '!' }]}>
                                <Radio.Group>
                                    <Radio value={0}></Radio>
                                    <Radio value={1}></Radio>
                                </Radio.Group>
                            </Form.Item>
                            <Form.Item label="" name="welfare" rules={[{ required: true, message: '!' }]}>
                                <Input.TextArea placeholder="" />
                            </Form.Item>
                            <Form.Item label="" name="specialGroup" rules={[{ required: true, message: '!' }]}>
                                <Input.TextArea placeholder="" />
                            </Form.Item>
                            <Form.Item label="" name="announcements" rules={[{ required: true, message: '!' }]}>
                                <Input.TextArea placeholder="" />
                            </Form.Item>
                            <Form.Item label="" name="treatment" rules={[{ required: true, message: '!' }]}>
                                <Input.TextArea placeholder="" />
                            </Form.Item>
                            <Form.Item label="" name="adminWechatCode" rules={[{ required: true, message: '!' }]} >
                                <Input placeholder="" />
                            </Form.Item>
                            <Form.Item label="" name="articleUrl" rules={[{ required: true, message: '!' }]} >
                                <Input placeholder="" />
                            </Form.Item>
                            <Form.Item label="" name="articleImgUrl" rules={[{message: '!' }]} >
                                <Input placeholder="OSS" />
                            </Form.Item>
                            <Form.Item label="" name="communityPeoples" rules={[{ required: true, message: '!' }]} >
                                <Input placeholder="" />
                            </Form.Item>
                            <Form.Item label="" onChange={onChange} name="shareWay" rules={[{ required: true, message: '!' }]}>
                                <Radio.Group>
                                    <Radio value={0}></Radio>
                                    <Radio value={1}></Radio>
                                    <Radio value={2}></Radio>
                                </Radio.Group>
                            </Form.Item>
                            {visible==0||visible==1?(
                                <Form.Item label="" name="shareNumber" rules={[{type: 'number', min: 1, required: true, message: '!' }]} >
                                    <InputNumber  />
                                </Form.Item>
                            ):''}
                            {visible==0||visible==2 ?(
                                <Form.Item label="" name="payAmount" rules={[{ required: true, message: '!' }]} >
                                    <Input placeholder="" />
                                </Form.Item>
                            ):''}
                            <Form.Item wrapperCol={{ offset: 10 }}>
                                <Button type="primary" htmlType="submit">
                                    
                                </Button>

                            </Form.Item>

                        </Form>

                    </div>
                </div>
            </div>
        </div>
    );
};
export default Application