import React, { useEffect, useState } from 'react'
import { Modal, Form, Input, message,Select, InputNumber } from 'antd'
import { axiosPost } from '@/extend/axios/index.js';
import '@/assets/css/modal.scss'
const FormItem = Form.Item;
const { Option } = Select

export default function ActionModal(props){
    const {type,title,visible,record} = props;
    const [form] = Form.useForm();    
    const formItemLayout = {
        labelCol: { span: 7 },
        wrapperCol: { span: 15 },
    };
    const handleOk = () => {
        form.validateFields().then(values => {
            console.log(values);
            const url = type === 'add' ? '/manage/wx/minipro/userConfig/save' : '/manage/wx/minipro/userConfig/update'
            axiosPost(url,values).then((res) => {            
                if(res.code == 0){                
                    message.success("");
                    handleCancel();
                    props.refresh();
                }else{
                    message.error(res.message);
                }          
            }).catch((error) => {
                console.log(error);
            });
        }).catch(error => {
            console.log(error);
        })
    }
    const handleCancel = () => {
        form.resetFields();
        props.hideModal();
    }
    useEffect(() => {
        type === 'update' && form.setFieldsValue(record)
    },[record]);
    return (
        <div>
            <Modal
            getContainer={false}
            width={600}
            className="modal-common"
            title={title}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            >
                <div className="modal-inner-connent">
                    <Form {...formItemLayout} form={form}>
                        {
                            type === 'update' && 
                            <FormItem name="id" noStyle={true}>
                                <Input type="hidden" />
                            </FormItem>
                        }
                        <FormItem label="" name="shareNum" rules={[{required: true, message: ''}]}>
                            <InputNumber placeholder="" />
                        </FormItem>
                        <FormItem label="" name="contactIntegral" rules={[{ required: true, message: ''}]}>
                            <InputNumber placeholder="" />
                        </FormItem>
                        <FormItem label="" name="memberAmount" rules={[{ required: true, message: ''}]}>
                            <InputNumber placeholder="" />
                        </FormItem>
                        <FormItem label="" name="payAmount" rules={[{ required: true, message: ''}]}>
                            <InputNumber placeholder="" />
                        </FormItem>
                        <FormItem label="" name="lookNum" rules={[{ required: true, message: ''}]}>
                            <InputNumber placeholder="" />
                        </FormItem>
                        <FormItem label="" name="recommenIntegral" rules={[{ required: true, message: ''}]}>
                            <InputNumber placeholder="" />
                        </FormItem>
                        <FormItem label="" name="signIntegral" rules={[{ required: true, message: ''}]}>
                            <InputNumber placeholder="" />
                        </FormItem>
                        <FormItem label="" name="integralRule" rules={[{ required: true, message: ''}]}>
                            <Input.TextArea placeholder="" />
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        </div>
    )
}